.aside {
  position: fixed;
  left: 0;
  top: 0;
  background-color: var(--body-color);
  border-right: 1px solid rgba(0, 0, 0, 0.05);
  padding: 2.5rem;
  width: 110px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  justify-content: space-around;
  z-index: 10;
  transition: 0.3s;
}

.nav__list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.nav__link {
  font-size: 1.5rem;
  color: var(--text-color);
  font-weight: var(--font-bold);
  transition: 0.3s;
}

.nav__link:hover,
.nav__link.active {
  color: hsl(43, 100%, 68%);
}

.nav__link span {
  position: absolute;
  transition: 0.3s;
  font-size: 12px;
  background: var(--first-color);
  left: 110%;
  color: #fff;
  display: block;
  padding: 3px 8px;
  border-radius: 5px;
  visibility: hidden;
  opacity: 0;
}
.nav__link span::before {
  content: "";
  width: 10px;
  height: 10px;
  background: var(--first-color);
  position: absolute;
  left: -3px;
  top: 50%;
  transform: rotate(54deg) skew(-8deg, -39deg);
  margin-top: -5px;
  z-index: -1;
}
.nav__link:hover span {
  opacity: 1;
  visibility: visible;
}

.copyright {
  color: hsl(245, 15%, 65%);
  font-size: var(--small-font-size);
  transform: rotate(-180deg);
  writing-mode: vertical-rl;
}

.nav__toggle {
  position: fixed;
  top: 1.25rem;
  left: 1.875rem;
  cursor: pointer;
  height: 40px;
  width: 45px;
  background-color: var(--body-color);
  border: 1px solid #e8dfec;
  border-radius: 10px;
  display: none;
  justify-content: center;
  align-items: center;
  z-index: 1;
  transition: 0.3s;
}

@media screen and (max-width: 1024px) {
  .aside {
    left: -110px;
  }

  .nav__toggle {
    display: flex;
  }

  .nav__toggle-open {
    left: 140px;
  }

  .show-menu {
    left: 0;
  }
}
